import React, { Component } from 'react';
import Calendar from '../assets/calendar.png';
import File from '../assets/file.png';
import Price from '../assets/Price.png';
import Stats from '../assets/statistics-report.png';
import Bitmap from '../assets/icon.png';
import DatePickerWrapper from './DatePickerWrapper';

export class Campaigns extends Component {

    constructor(props) {
        super(props)
    
        this.state= {
            startDate: new Date()
        }

        this.headers=['name','region','createdOn','']
    }


    render() {
        const { campaigns } = this.props;
        return (
            <table>
                <thead>
                    <tr>
                        <th>Date</th>
                        <th>Campaign</th>
                        <th>View</th>
                        <th>Actions</th>
                    </tr>
                </thead>
                <tbody>
                    { campaigns.map((campaign, ind)=>{
                        return <tr key={ind}>
                            <td>
                                {campaign.formattedDate} 
                                <span className="block">{campaign.diff}</span>
                            </td>
                            <td>
                                <img className="big-img" src={Bitmap}/>
                                <span>
                                    {campaign.name} 
                                    <span className="block">{campaign.region}</span>
                                </span>
                            </td>
                            <td>
                                <span className="block">
                                    <img src={Price}/>
                                    <a href="javascript:void(0)" onClick={(e)=>this.props.showModalFunc(e, campaign)}>View Pricing</a>
                                </span>
                            </td>
                            <td>
                                <span className="margin-10">
                                    <img src={File}/>
                                    <a href={campaign.csv}>CSV</a>
                                </span>
                                <span className="margin-10 margin-l-10">
                                    <img src={Stats}/>
                                    <a href={campaign.report}>Report</a>
                                </span>
                                <span className="margin-10 margin-l-10">
                                    <img src={Calendar}/>
                                    <DatePickerWrapper
                                        campaign={campaign}
                                        ind={ind}
                                        changeTime={this.props.changeTime}
                                    />
                                </span>
                            </td>
                        </tr>
                        })
                    }   
                </tbody>
            </table>
        )
    }
}

export default Campaigns
