import React, { Component } from 'react';
import moment from 'moment';
import Popup from './Popup';
import Campaigns from './campaigns';
import Axios from 'axios';

export class Main extends Component {
  constructor(props) {
    super(props)

    this.state = {
      currentTab: 'upcoming',
      campaigns: [],
      live: [],
      past: [],
      upcoming: [],
      showModal:false,
      selectedCampaign:null
    }
    this.date = moment()
  }


  componentDidMount() {
    Axios.get(`https://run.mocky.io/v3/b4435460-af4a-42a2-8505-9c38e5d5be69`)
      .then(res => {
        let campaigns = res['data']['data'];
        this.initializeData(campaigns);
      })
      .catch(err => {
        console.log(err);
        this.setState({
          campaigns: []
        })
      })
  }

  initializeData=(campaigns)=>{
    let { past, live, upcoming } = this.filterCampaigns(campaigns);
    this.setState({
      campaigns: campaigns,
      live: live,
      upcoming: upcoming,
      past: past,
    })
  }

  tabChange = (tab) => {
    this.setState({
      currentTab: tab
    })
  }

  showModalFunc=(e, campaign)=>{
    e.preventDefault();
    this.setState({
      showModal:true,
      selectedCampaign:campaign
    })
  }

  hideModalFunc=(e)=>{
    e.preventDefault();
    this.setState({
      showModal:false,
      selectedCampaign:null
    })
  }

  filterCampaigns = (campaigns) => {
    let todayDate = this.date.date();
    let todayMonth = this.date.month();
    let todayYear = this.date.year();
    let past = [];
    let live = [];
    let upcoming = [];
    let diff, absDiff;
    let date;
    campaigns.forEach(campaign => {
      date = moment(campaign.createdOn);
      campaign['formattedDate']=date.format('MMMM DD, yyyy')
      diff = todayYear - date.year();
      absDiff = Math.abs(diff);
      if (diff > 0) {
        campaign['diff'] = absDiff + ' years ago';
        past.push(campaign);
      } else if (diff < 0) {
        campaign['diff'] = absDiff + ' years ahead';
        upcoming.push(campaign);
      } else {
        diff = todayMonth - date.month();
        absDiff = Math.abs(diff);
        if (diff > 0) {
          campaign['diff'] = absDiff + ' months ago';
          past.push(campaign);
        } else if (diff < 0) {
          campaign['diff'] = absDiff + ' months ahead';
          upcoming.push(campaign);
        } else {
          diff = todayDate - date.date();
          absDiff = Math.abs(diff);
          if (diff > 0) {
            campaign['diff'] = absDiff + ' days ago';
            past.push(campaign);
          } else if (diff < 0) {
            campaign['diff'] = absDiff + ' days ahead';
            upcoming.push(campaign);
          } else {
            campaign['diff'] = 'Live';
            live.push(campaign);
          }
        }
      }
    });
    return {
      live: live,
      upcoming: upcoming,
      past: past
    }
  }

  changeTime=(campaign, date)=>{
    for(let i=0;i<this.state.campaigns.length;i++){
      if(this.state.campaigns[i].id==campaign.id) {
        campaign['createdOn']=date;
      }
    }
    this.setState({
      live:[],
      upcoming:[],
      past:[]
    }, ()=>{
      this.initializeData(this.state.campaigns);
    })
  }

  render() {
    const { campaigns, currentTab, showModal, selectedCampaign } = this.state;
    return (
      <div className="container">

        <div className="tab">
          <button className={"tablinks " + (currentTab == 'upcoming' ? 'active' : '')}
            onClick={() => this.tabChange('upcoming')}>Upcoming Campaigns</button>
          <button className={"tablinks " + (currentTab == 'live' ? 'active' : '')}
            onClick={() => this.tabChange('live')}>Live Campaigns</button>
          <button className={"tablinks " + (currentTab == 'past' ? 'active' : '')}
            onClick={() => this.tabChange('past')}>Past Campaigns</button>
        </div>

        {campaigns.length > 0 && <div className="tabcontent">
          {currentTab == 'upcoming' && 
            <Campaigns 
              campaigns={this.state.upcoming} 
              showModalFunc={this.showModalFunc} 
              changeTime={this.changeTime}
              changeTime={this.changeTime}
            />}
          {currentTab == 'live' && 
            <Campaigns 
              campaigns={this.state.live} 
              showModalFunc={this.showModalFunc} 
              changeTime={this.changeTime}
            />}
          {currentTab == 'past' && 
            <Campaigns 
              campaigns={this.state.past} 
              showModalFunc={this.showModalFunc} 
              changeTime={this.changeTime}
            />}
        </div>}

        {showModal && 
            <Popup 
                campaign={selectedCampaign}
                hideModalFunc={this.hideModalFunc}
            />
        }
      </div>

    );
  }
}

export default Main;
