import React, { Component } from 'react';
import DatePicker from 'react-datepicker';
import "react-datepicker/dist/react-datepicker.css";

export class DatePickerWrapper extends Component {

    handleChange = (date) => {
        this.setState({
            startDate: date
        });
        this.props.changeTime(this.props.campaign, date.getTime())
    };

    selectCampaign = (campaign) => {
        console.log(campaign)
    }
    render() {
        const { campaign } = this.props;
        return (
            <DatePicker
                selected={campaign.createdOn}
                onChange={this.handleChange}
                customInput={<a href="javascript:void(0);" onClick={(campaign) => this.selectCampaign(campaign)}>Schedule Again</a>}
            />
        )
    }
}

export default DatePickerWrapper
